import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next"; // استيراد useTranslation

const HeroSection = () => {
  const { t, i18n } = useTranslation(); // استخدام الترجمة و i18n لتغيير اللغة
  const [searchTerm, setSearchTerm] = useState("");
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  const changeLanguage = (lang) => {
    i18n.changeLanguage(lang); // تغيير اللغة باستخدام i18n
  };

  const search = () => {
    console.log(`Searching for: ${searchTerm}`);
    // أضف هنا منطق البحث الفعلي
  };

  const languageFlags = {
    ar: "https://flagcdn.com/w320/sa.png", // علم الإمارات (مثال للغة العربية)
    us: "https://flagcdn.com/w320/us.png", // علم الولايات المتحدة (مثال للإنجليزية)
    es: "https://flagcdn.com/w320/es.png", // علم إسبانيا
    fr: "https://flagcdn.com/w320/fr.png", // علم فرنسا
    de: "https://flagcdn.com/w320/de.png", // علم ألمانيا
    it: "https://flagcdn.com/w320/it.png", // علم إيطاليا
  };

  // صور لتغيير الخلفية
  const images = [
    "img1.jpeg",
    "img2.jpeg",
    "img3.jpeg",
    "img4.jpg",
    "img5.jpeg",
  ];

  // تغيير الصورة كل 4 ثوانٍ
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImageIndex((prevIndex) => (prevIndex + 1) % images.length);
    }, 4000);

    return () => clearInterval(interval); // تنظيف المؤقت عند الخروج
  }, [images.length]);

  return (
    <section
      className="hero custom-hero"
      style={{
        backgroundImage: `url(${images[currentImageIndex]})`,
        transition: "background-image 1s ease-in-out", // تأثير الانتقال
      }}
    >
      <div className="container text-white text-center pt-5">
        <h1>{t("hero.heading")}</h1> {/* استخدام الترجمة هنا */}
        <p className="lead">{t("hero.description")}</p>
        <a href="#services" className="btn btn-light mt-3">
          {t("hero.button")}
        </a>
        <div className="search-container mt-4">
          <input
            type="text"
            id="search"
            className="search-input"
            placeholder={t("hero.searchPlaceholder")}
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <button className="search-button" onClick={search}>
            <i className="fas fa-search"></i>
          </button>
        </div>
        <div className="language-selector mt-4">
          {["ar", "us", "es", "fr", "de", "it"].map((lang, index) => (
            <div
              key={index}
              className="language-option"
              onClick={() => changeLanguage(lang)}
            >
              <img
                src={languageFlags[lang]}
                alt={`علم ${lang}`}
                style={{ width: "20px", height: "15px" }}
              />
              <span>
                {lang === "ar"
                  ? "العربية"
                  : lang === "us"
                    ? "English"
                    : lang === "es"
                      ? "Español"
                      : lang === "fr"
                        ? "Français"
                        : lang === "de"
                          ? "Deutsch"
                          : "Italiano"}
              </span>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
